<template>
    <div>
      <MHeader :back="true">列表</MHeader>
      <keep-alive>
        <div class="content"  id="searchBar" @scroll="loadMore" ref="contentbox">
          <ul>
            <router-link v-for="(book,index) in books" :to="{name:'detail',params:{bid:book.bookId}}" :key="index" tag="li">
              <img v-lazy="book.bookCover" alt="">
              <div class="list-r">
                <h4>{{book.bookName}}</h4>
                <p>{{book.booeInfo}}</p>
                <p> 单价：{{book.bookPrice}}</p>
                <button @click.stop="delBook(book.bookId)">删除{{book.bookId}}</button>
              </div>
            </router-link>
          </ul>
          <button @click="getData" class="more">加载更多</button>
        </div>
      </keep-alive>
    </div>
</template>

<script>
  import {pagination,removeBook} from '../api'
  import MHeader from '@/base/MHeader.vue'
    export default {
      data() {
          return {
            books:[],
            offset:0,//当前偏移量
            hasMore:true,//是否有更多
            isLoading:false,//if正在加载
          }
      },
      created () {
        this.more();
      },
      mounted () {
        this.pullFresh();
      },
      methods: {
        more(){
          this.getData();
        },
        loadMore(){
          clearTimeout(this.timer);
          //节流,进来时，触发scroll事件 可能触发n次 先进来开一个定时器 下次触发时将上一次的定时器清除掉
          this.timer=setTimeout(()=>{
            //  卷曲的高度    当前课件区域   总高
            let {scrollTop,clientHeight,scrollHeight}=this.$refs.contentbox;
            if(scrollTop+clientHeight+20>=scrollHeight){
              this.getData();
            }
          },50)
        },
        pullFresh(){
          let scroll=this.$refs.contentbox;//获取要拖拽的元素
          let top=scroll.offsetTop;
          let distance=0;
          scroll.addEventListener('touchstart',(e)=>{
            //滚动条在最顶端并且盒子在顶端时才触发
            if(scroll.style.top!=0 && scroll.style.offsetTop==top)return;
            let start=e.touches[0].pageY;
            console.log(start);
            let move=(e)=>{
              let current=e.touches[0].pageY;
              distance=current-start;//拉动的距离
              if(distance>0){//向下拉
                if(distance<=100){
                  scroll.style.top=distance+top+'px';
                }else{
                  distance=100;
                  scroll.style.top=top+100+'px';
                }
              }else{
                //不在考虑范围内，移除事件
                scroll.removeEventListener('touchmove',move)
                scroll.removeEventListener('touchend',end)
              }
            };
            let end=(e)=>{
              clearInterval(this.timer1);
              this.timer1=setInterval(()=>{//一共distance 每次-1
                if(distance<=0){
                  clearInterval(this.timer1);
                  distance=0;
                  scroll.style.top=50+'px';
                  scroll.removeEventListener('touchmove',move)
                  scroll.removeEventListener('touchend',end)

                  this.books=[]; //线清空数据
                  this.offset=0;
                  this.getData();
                  return;
                }
                distance-=1;
                scroll.style.top=top+distance+'px';
              },1)
            };
            //不在考虑范围内，移除事件
          scroll.addEventListener('touchmove',move)
          scroll.addEventListener('touchend',end)
          },false)
        },
        async getData(){
          this.isLoading=true;
          if(this.hasMore){//有更多的时候获取数据
            let {hasMore,books}=await pagination(this.offset);
            this.books=[...this.books,...books];//获取的书放到books属性上
            this.hasMore=hasMore;
            this.isLoading=false;//加载完毕
            this.offset=this.books.length;//维护偏移量 就是书的总长度
          }
          // this.books=await getBooks();
        },
        async delBook(id){
          await removeBook(id);
          this.books=this.books.filter(item=>item.bookId!==id);
        },

      },
      computed: {},
      components: {MHeader,}
    }
</script>

<style scoped>
  .content {
    position: absolute;

  }
  .content ul li {
    display:flex;
    padding: 10px;
    margin: 10px;
    border:1px solid cadetblue;
  }
  .content ul li img{
    width: 30%;
    height:15%;
  }
  .list-r{
    margin:10px;
    text-align:left;
  }
  .list-r button{
    display:block;
    background: orangered;
    color: white;
    font-weight: bold;

    width: 50px;
    height: 25px;
    border: none;
    border-radius: 5px;
    margin-top: 10px;

  }
  .content h4{
    font-size: 20px;
    line-height: 25px;
  }
  .content p{
    color: #2a2a2a;
    line-height: 25px;
  }
  .more{
    position: fixed;
    bottom:50px;
    right: 50px;
    display:block;
    background: #8be5de;
    color: #232222;
    font-weight: bold;

    width:100px;
    height: 25px;
    border: none;
    border-radius: 5px;
    margin-top: 10px;
  }
</style>



